import React, { Component } from 'react'
import {NavLink , withRouter} from "react-router-dom";
import { SwitchTransition , Transition} from 'react-transition-group';
import { setFullHeight } from '../utils/functions';
import {TimelineLite} from "gsap";
import {UnderlineInput ,EmailUnderlineInput , PhoneUnderlineInput } from "../components/general/forms";
import Cookies from  "js-cookie";
// styles 
import "../../sass/components/pages/_contacts.scss";

const { TelegramClient } = require('messaging-api-telegram');



class SubmitMessage extends Component
{
    constructor(props)
    {
        super(props);


        this.state=
        {
            email: {
                valid: this.props.parent.state.email !== "",
                value: this.props.parent.state.email,
            },
            phone: {
                valid : this.props.parent.state.phone !== "",
                value: this.props.parent.state.phone,
            },
            name: {
                valid: this.props.parent.state.name !== "",
                value: this.props.parent.state.name,
            },
            message: {
                valid: this.props.parent.state.message !== "",
                value: this.props.parent.state.message,
            }
        }

        

        // ---- refs ---- //
        this.client = TelegramClient.connect('623347103:AAE5wFo_80Kdh6pJ5_-rbf7XKntM7S4wtTk');
        this.messageInput = React.createRef();

        // ---- vars ---- //

        this._animationIn = null;
        this._animationOut = null;

        // ---- bindings ---- //

        this.onEndHandler = this.onEndHandler.bind(this);

        this.onEmailInputHandler = this.onEmailInputHandler.bind(this);
        this.onPhoneInputHandler = this.onPhoneInputHandler.bind(this);
        this.onNameInputHandler = this.onNameInputHandler.bind(this);
        this.onMessageChangeHandler = this.onMessageChangeHandler.bind(this);

        this.onEmailValidHandler = this.onEmailValidHandler.bind(this);
        this.onPhoneValidHandler = this.onPhoneValidHandler.bind(this);
        this.onNameValidHandler = this.onNameValidHandler.bind(this);

        this.onEmailInvalidHandler = this.onEmailInvalidHandler.bind(this);
        this.onPhoneInvalidHandler = this.onPhoneInvalidHandler.bind(this);
        this.onNameInvalidHandler = this.onNameInvalidHandler.bind(this);

        this.onSubmitEventHandler = this.onSubmitEventHandler.bind(this);

        // animations //
        this.In = this.In.bind(this);
        this.Out = this.Out.bind(this);

    }

    onMessageChangeHandler(e)
    {
        const value = this.messageInput.current.value;

        if(value !== "")
        {
            if(!this.state.message.valid)
            {
                this.setState({
                    message:{
                        valid : true,
                        value : value,
                    }
                });

                return;
            }
        }

        if(value === "")
        {
            if(this.state.message.valid)
            {
                this.setState({
                    message:{
                        valid : false,
                        value : value
                    }
                });

                return;
            }
        }
        

        this.setState({
            message:{
                value : value,
                valid : this.state.message.valid,
            }
        })
    }

    componentDidUpdate(prevProps , prevState)
    {
        if(this.state.email.value !== this.props.parent.state.email)
        {
            this.props.parent.setState({
                email:this.state.email.value,
            });
        }

        if(this.state.phone.value !== this.props.parent.state.phone)
        {
            this.props.parent.setState({
                phone:this.state.phone.value,
            });
        }

        if(this.state.name.value !== this.props.parent.state.name)
        {
            this.props.parent.setState({
                name:this.state.name.value,
            });
        }
    }

    onSubmitEventHandler()
    {
        if( !(this.state.email.valid && this.state.message.valid && this.state.name.valid && this.state.phone.valid ))
        {
            return;
        }

        let message = "";

        message += `email: ${this.state.email.value} \n`;
        message += `phone: ${this.state.phone.value} \n`;
        message += `name: ${this.state.name.value} \n`;
        message += `messge: ${this.state.message.value} \n`;

        this.client.sendMessage('-1001159072650' , message );
    }
    // ---- onInput handlers ---- //

    onEmailInputHandler(value)
    {
        this.setState({
            email: 
            {
                value:value,
                valid: this.state.email.valid,
            }
        });
    }

    onPhoneInputHandler(value)
    {
        this.setState({
            phone:
            {
                value:value,
                valid: this.state.phone.valid,
            }
        });
    }

    onNameInputHandler(value)
    {
        this.setState({
            name:
            {
                value:value,
                valid: this.state.name.valid,
            }
        });
    }

    // ---- onValid events ---- //

    onEmailValidHandler(value)
    {
        if(!this.state.email.valid)
        {
            this.setState({
                email:
                {
                    valid:true,
                    value:this.state.email.value
                }
            });
        }
    }

    onPhoneValidHandler(value)
    {

        if(!this.state.phone.valid)
        {
            this.setState({
                phone:
                {
                    valid:true,   
                    value: this.state.phone.value, 
                }
            });
        }
    }

    onNameValidHandler(value)
    {
        if(!this.state.name.valid)
        {
            this.setState({
                name:
                {
                    valid:true,   
                    value: this.state.name.value 
                }
            });
        }
    }


    // ---- onInvalid events ---- //

    onEmailInvalidHandler(value)
    {
        if(this.state.email.valid)
        {
            this.setState({
                email:
                {
                    valid:false,    
                    value: this.state.email.value 
                }
            });
        }
    }
    onPhoneInvalidHandler(value)
    {
        if(this.state.phone.valid)
        {
            this.setState({
                phone:
                {
                    valid:false,    
                    value: this.state.phone.value 
                }
            });
        }
    }

    onNameInvalidHandler(value)
    {
        if(this.state.name.valid)
        {
            this.setState({
                name:
                {
                    valid:false,    
                    value: this.state.phone.value 
                }
            });
        }
    }


    In(callback)
    {
        if(callback) callback();
    }


    Out(callback)
    {

        if(callback) callback();
    }



    onEndHandler(n , done)
    {
        if(this.props.in)
        {
            this.In(done);
        }
        else 
        {
            this.Out(done);
        }
    }

    // ---- life cycle events ---- //

    render()
    {
        return(
            <Transition
            {...this.props}
            appear={true}
            enter={true}
            exit={true}
            mountOnEnter={true}
            unmountOnExit={true}
            timeout={2000}
            addEndListener={this.onEndHandler}
            >
            <div className="contact-us-item-container d-flex flex-column justify-content-end submit-message">
                <div className="contact-us-item">
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-md-4">
                                <div className="contact-us-item-group">
                                    <h5 id="email" className="bold uppercase white">email</h5>
                                    <div className="form-container">
                                        <EmailUnderlineInput
                                            onInput={this.onEmailInputHandler}
                                            onValid={this.onEmailValidHandler}
                                            onInvalid={this.onEmailInvalidHandler}
                                            defaultValue={this.state.email.value}
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-4">            
                                <div className="contact-us-item-group">
                                    <h5 id="phone" className="bold uppercase white">phone number</h5>
                                    <div className="form-container">
                                        <PhoneUnderlineInput
                                            onInput={this.onPhoneInputHandler}
                                            onValid={this.onPhoneValidHandler}
                                            onInvalid={this.onPhoneInvalidHandler}
                                            defaultValue={this.state.phone.value}
                                        />
                                    </div>
                                </div>
                            </div>
                            <div className="col-md-4">
                                <div className="contact-us-item-group">
                                    <h5 id="name" className="bold uppercase white">name</h5>
                                    <div className="form-container">
                                        <UnderlineInput
                                            onInput={this.onNameInputHandler}
                                            onValid={this.onNameValidHandler}
                                            onInvalid={this.onNameInvalidHandler}
                                            defaultValue={this.state.name.value}
                                        />
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-md-12">
                                <div className="contact-us-item-group">
                                    <h5 id="email" className="bold uppercase white">message</h5>
                                    <div className="form-container">
                                        <div className="input-group">
                                            <textarea placeholder="John Doe" defaultValue={this.props.parent.state.message}/>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="contact-us-item-footer">
                    <NavLink to={"/contact/message"}>
                        <span id="back-link" className="uppercase white text-with-icon reverse">
                            <span className="text">back</span>
                            <span className="arrow">
                                <i className="icon icon-left-chevron"/>
                                <span className="line"/>
                            </span>
                        </span>
                    </NavLink>
                    <NavLink to={"/contact/submit"} onClick={this.navLinkClickHandler}>
                        <span id="forward-link" className="uppercase white text-with-icon">
                            <span className="text" onClick={this.onSubmitEventHandler}>submit</span>
                        </span>
                    </NavLink>
                </div>
            </div>
            </Transition>
        )
    }
}

