class Phone extends Component
{
    constructor(props)
    {
        super(props);


        this.state={
            phone: this.props.parent.state.phone,
            valid: this.props.parent.state.phone !== ""
        }
        // ---- vars ---- //
        this._animationIn = null;;
        this._animationOut = null;

        // ---- bindings ---- //

        this.onEndHandler = this.onEndHandler.bind(this);
        this.navLinkClickHandler = this.navLinkClickHandler.bind(this);
        this.onInvalidEventHandler = this.onInvalidEventHandler.bind(this);
        this.onValidEventHandler = this.onValidEventHandler.bind(this);
        this.onSubmitHandler = this.onSubmitHandler.bind(this);
        this.onInputEventHandler = this.onInputEventHandler.bind(this);

        // animations //
        this.In = this.In.bind(this);
        this.Out = this.Out.bind(this);

    }


    onInputEventHandler(value)
    {
        this.setState({
            phone:value,
        });
    }

    In(callback)
    {
        if(callback) callback();
    }


    Out(callback)
    {

        if(callback) callback();
    }

    onValidEventHandler(value)
    {
        if(!this.state.valid)
        {
            this.setState({valid:true});
        }
    }

    onInvalidEventHandler(value)
    {
        if(this.state.valid)
        {
            this.setState({valid:false});
        }
    }

    onSubmitHandler(value)
    {
        if(this.state.valid)
        {
            if(this.props.parent.state.phone !== this.state.phone)
            {
                this.props.parent.setState({
                    phone: this.state.phone
                });
            }

            this.props.history.push("/contact/name");
        }
    }

    navLinkClickHandler(e)
    {
        if(!this.state.valid)
        {
            e.preventDefault();
            return;
        }

        if(this.props.parent.state.phone !== this.state.phone)
        {
            this.props.parent.setState({
                phone: this.state.phone
            });
        }
    }


    onEndHandler(n , done)
    {
        if(this.props.in)
        {
            this.In(done);
        }
        else 
        {
            this.Out(done);
        }
    }

    // ---- life cycle events ---- //

    render()
    {
        return(
            <Transition
            {...this.props}
            appear={true}
            enter={true}
            exit={true}
            mountOnEnter={true}
            unmountOnExit={true}
            timeout={2000}
            addEndListener={this.onEndHandler}
            >
            <div className="contact-us-item-container d-flex flex-column justify-content-end contact-us-form">
                <div className="contact-us-item">
                    <h2 id="email" className="bold uppercase white">phone</h2>
                    <div className="form-container">
                        <PhoneUnderlineInput
                            onValid={this.onValidEventHandler}
                            onInvalid={this.onInvalidEventHandler}
                            onSubmit={this.onSubmitHandler}
                            defaultValue={this.state.phone}
                            placeholder="000-000-00-00"
                            onInput={this.onInputEventHandler}
                        />
                    </div>
                </div>
                <div className="contact-us-item-footer">
                    <NavLink to={"/contact/email"}>
                        <span id="back-link" className="uppercase white text-with-icon reverse">
                            <span className="text">back</span>
                            <span className="arrow">
                                <i className="icon icon-left-chevron"/>
                                <span className="line"/>
                            </span>
                        </span>
                    </NavLink>
                    <NavLink to={"/contact/name"} onClick={this.navLinkClickHandler}>
                        <span id="forward-link" className="uppercase white text-with-icon">
                            <span className="text">name</span>
                            <span className="arrow">
                                <span className="line"/>
                                <i className="icon icon-right-chevron"/>
                            </span>
                        </span>
                    </NavLink>
                </div>
            </div>
            </Transition>
        )
    }
}
class Email extends Component 
{
    constructor(props)
    {
        super(props);

        this.state= 
        {
            valid: this.props.parent.state.email !== "",
            email: this.props.parent.state.email,
        }

        this.enter = new TimelineLite({paused:true});
        this.exit = new TimelineLite({paused:true});

        this.input = React.createRef();

        this.onEndHandler = this.onEndHandler.bind(this);
        this.navLinkClickHandler = this.navLinkClickHandler.bind(this);
        this.onSubmitHandler = this.onSubmitHandler.bind(this);
        this.onValidEventHandler = this.onValidEventHandler.bind(this);
        this.onInvalidEventHandler = this.onInvalidEventHandler.bind(this);
        this.onInputEventHandler = this.onInputEventHandler.bind(this);
 

    }

    onInputEventHandler(value)
    {
        this.setState({
            email: value,
        });
    }
    onValidEventHandler(value)
    {
        if(!this.state.valid)
        {
            this.setState({
                valid: true,
            });
        }
    }

    onInvalidEventHandler(value)
    {
        if(this.state.valid)
        {
           this.setState({
               valid:false,
           }) 
        }
    }

    navLinkClickHandler(e)
    {
        if(!this.state.valid)
        {
            e.preventDefault();
            return;
        }

        if(this.props.parent.state.email !== this.state.email)
        {
            this.props.parent.setState({
                email:this.state.email,
            });
        }

    }

    onSubmitHandler(value)
    {
        if(this.state.valid)
        {
            if(this.props.parent.state.email !== value)
            {
                this.props.parent.setState({email : value,});
            }

            this.props.history.push("/contact/phone");
        }
    }

    onEndHandler(n,done)
    {
        
        if(this.props.in)
        {
            
            this.enter.fromTo("#email" , 1 , {opacity:0, x:"-100%"} , {opacity:1 , x:"0%"});
            this.enter.play(0);
        }

        if(!this.props.in)
        {
            this.exit.fromTo("#email" , 0.25 , {opacity:0, x:"-100%"} , {opacity:1 , x:"0%" , onComplete:done});
            this.exit.play(0);
        }
    }

    render()
    {
        return (
            <Transition
            {...this.props}
            appear={true}
            enter={true}
            exit={true}
            mountOnEnter={true}
            unmountOnExit={true}
            timeout={1200}
            addEndListener={this.onEndHandler}
            >
            <div className="contact-us-item-container d-flex flex-column justify-content-end contact-us-form">
                <div className="contact-us-item">
                    <h2 id="email" className="bold uppercase white">email</h2>
                    <div className="form-container">
                        <EmailUnderlineInput
                            validate={this.validate}
                            onSubmit={this.onSubmitHandler}
                            defaultValue={this.state.email}
                            onValid={this.onValidEventHandler}
                            onInvalid={this.onInvalidEventHandler}
                            onInput={this.onInputEventHandler}
                            placeholder={"email"}
                        />
                    </div>
                </div>
                <div className="contact-us-item-footer">
                    <NavLink to={"/contact/"}>
                        <span id="back-link" className="uppercase white text-with-icon reverse">
                            <span className="text">back</span>
                            <span className="arrow">
                                <i className="icon icon-left-chevron"/>
                                <span className="line"/>
                            </span>
                        </span>
                    </NavLink>
                    <NavLink to={"/contact/phone"} onClick={this.navLinkClickHandler}>
                        <span id="forward-link" className="uppercase white text-with-icon">
                            <span className="text">phone</span>
                            <span className="arrow">
                                <span className="line"/>
                                <i className="icon icon-right-chevron"/>
                            </span>
                        </span>
                    </NavLink>
                </div>
            </div>
            </Transition>
        )
    }
}

class AnyQuestions extends Component
{
    constructor(props)
    {
        super(props);
    
        this.endHandler = this.endHandler.bind(this);

        this.enter  = new TimelineLite({paused:true});
        this.exit   = new TimelineLite({paused:true});

    }


    endHandler(node , done)
    {
        
        if(this.props.in)
        {
            this.enter.onComplete = ()=>{
            }
            this.enter.fromTo("#any" , 0.35 , {opacity:0 , x:"-50%"} , {opacity:1 , x:"0%"})
            .fromTo("#questions" , 0.35 , {opacity:0 , x:"-50%"} , {opacity:1 , x:"0%"} , "-=0.05")
            .fromTo("#answer" , 0.35 , {opacity:0} , {opacity:1 , onComplete:()=>{
                done();
            }});
            this.enter.play(0);
        }

        else
        {
            this.exit.to("#any" , 0.35 , {opacity:0 , x:"-50%"})
            .to("#questions" , 0.35 ,  {opacity:0 , x:"-100%"} , "-=0.05")
            .to("#answer" , 0.35 , {opacity:0 , onComplete: ()=>{
                done();       
            }});
            this.exit.play(0);
        }
    }

    render()
    {
        return(
            <Transition
            {...this.props}
            enter={true}
            appear={true}
            exit={true}
            mountOnEnter={true}
            unmountOnExit={true}
            addEndListener={this.endHandler}
            >
                <div className="contact-us-item-container d-flex flex-column justify-content-end">
                    <div className="contact-us-item">
                        <h2 id="any" className="bold uppercase white">Any</h2>
                        <h2 id="questions" className="bold uppercase stroke">questions?</h2>
                    </div>
                    <div className="contact-us-item-footer">
                        <NavLink to={"/contact/email"}>
                            <span id="answer" className="uppercase white text-with-icon">
                                <span className="text">yes</span>
                                <span className="arrow">
                                    <span className="line"/>
                                    <i className="icon icon-right-chevron"/>
                                </span>
                            </span>
                        </NavLink>
                    </div>
                </div>
            </Transition>
        )
    }
}



const components = [AnyQuestions , Email , Phone , ClientName , ClientMessage, SubmitMessage];

 class Contact extends React.Component {
    
    constructor(props)
    {
        super(props);

        if(this.props.current === undefined || this.props.current === null)
        {
            throw new Error("current was undefined or null ");
        }

        if(this.props.current >= components.length)
        {
            throw new Error("current property was out of range");
        }

        this.state =
        {
            current : this.props.current || 0,
            email   : this.props.email   ||  Cookies.get('user-email') || '',
            phone   : this.props.phone   ||  Cookies.get('user-phone') || '',
            message : this.props.message ||  Cookies.get('user-message') || '',
            name    : this.props.name    ||  Cookies.get('user-name') || '',

        }

        // ---- refs ---- //

        this.container = React.createRef();

        // ---- bindings ---- //
        this.updateHeight = this.updateHeight.bind(this);
        this.next = this.next.bind(this);
    }

    next()
    {
        this.setState({
            current:this.props.current + 1
        })
    }
    updateHeight()
    {
        setFullHeight(this.container.current);
    }

    componentDidMount()
    {
        this.updateHeight();

        window.addEventListener("resize" , this.updateHeight);
    }

    componentDidUpdate(prevProps , prevState)
    {
        if(prevState.name !== this.state.name)
        {
            Cookies.set("user-name" , this.state.name);
        }

        if(prevState.message !== this.state.message)
        {
            Cookies.set("user-message" , this.state.message)
        }

        if(prevState.phone !== this.state.phone)
        {
            Cookies.set("user-phone" , this.state.phone);
        }

        if(prevState.email !== this.state.email)
        {
            Cookies.set("user-email" , this.state.email);
        }

        if(this.props.current === undefined || this.props.current === null)
        {
            throw new Error("current was undefined or null ");
        }

        if(this.props.current >= components.length)
        {
            throw new Error("current property was out of range");
        }

        if(this.props.current !== this.state.current)
        {
            this.setState({
                current:this.props.current
            });
        }
    }

    componentWillUnmount()
    {
        window.removeEventListener("resize" , this.updateHeight);
    }

    render() 
    {

        let Component = components[this.state.current];
        return(
            <div className="contact-us-container">
                <div className="wrapper">
                    <div ref={this.container} className="contact-us d-flex align-items-end">
                        <SwitchTransition>
                            <Component key={this.state.current} parent={this} history={this.props.history} />
                        </SwitchTransition>
                    </div>
                </div>
            </div>
        )    
    }
}

export default withRouter(Contact);
