 class Navigation extends React.Component
{

    constructor(props)
    {
        super(props);

        this.state = {
            mobile: window.innerWidth < 768, 
        }
        // ---- ref's ---- //
        this.container = React.createRef();
        // ---- bindings ---- //
        this.updateNavigation = throttle(this.updateNavigation.bind(this) , 200);
        this.transitionEndHandler = this.transitionEndHandler.bind(this);
    }

    
    updateNavigation()
    {
        if(window.innerWidth > 768 && this.state.mobile)
        {
            this.setState({
                mobile:false,
            });
        }
        else if(window.innerWidth <= 768 && !this.state.mobile)
        {
            this.setState({
                mobile:true,
            })
        }
    }


    // ---- event listeners ---- //

    transitionEndHandler(n , done)
    {
        done();
    }

    // ---- lifecycle events ---- //

    componentDidMount()
    {
        window.addEventListener("resize" , this.updateNavigation);
    }

    componentWillUnmount()
    {
        window.removeEventListener("resize" , this.updateNavigation);
    }




    render()
    {

        return (
        <Transition {...this.props}
            enter={true}
            exit={true}
            mountOnEnter={true}
            unmountOnExit={true}
            addEndListener={this.transitionEndHandler}
        >
            <AppContext.Consumer>
                {(context)=>(
                    <header ref={this.container} cl className={`fixed ${context.mobile_navigation ? "dark" : ""}`}>
                        <div className="wrapper d-flex align-items-center justify-content-between">
                            <div className="logo-container">
                                <NavLink to="/">
                                    <img src="../img/logo.svg" alt="tpi" className="logo"/>
                                </NavLink>
                            </div>
                            {this.state.mobile ? <Hamburger active={false} onClick={this.onMenuClick}/> : <PrimaryMenu/>}
                        </div>
                    </header>
                )}
            </AppContext.Consumer>
        </Transition>
        );
    }
}




export default withRouter(Navigation);