import React from "react";
import {NavLink} from "react-router-dom";
import {gsap} from "gsap";
import {Transition} from "react-transition-group";
import AppContext from "../../utils/context";


import "../../../sass/components/general/_mobile-nav.scss";

 class MobileNav extends React.Component
{
    constructor(props)
    {
        super(props);

        this.state={
            show:this.props.show,
        }

        // ---- refs ---- //

        this.overlay = React.createRef();
        this.links = React.createRef();
        this.container = React.createRef();
        this.socialLinks = React.createRef();


        // bindings 

        this._enter = gsap.timeline();
        this._leave = gsap.timeline();
        this.onEndHandler = this.onEndHandler.bind(this);

    }



    // ---- lifecycle events ---- //


    componentDidUpdate(prevProps)
    {
        if(this.props.show !== prevProps.show)
        {
            this.setState({
                show:this.props.show,
            });
        }
    }


    onEndHandler(n , done)
    {

        if(this.props.in)
        {
            document.body.style.overflow="hidden";
            this._enter.fromTo(this.overlay.current , 0.65 , {y:"-100%"} , {y: "0%"})
            .staggerFromTo(this.links.current.children , 0.6 , {opacity:0} , {opacity:1 , onComplete:done} , 0.1, "-=0.45")
            .staggerFromTo(this.socialLinks.current.children , 0.6 , {opacity:0 , y:"-10px"} , {opacity:1 ,y:"0px", onComplete:done} , 0.15, "-=0.45")
        }
        else
        {
            document.body.style.overflow="";
            this._leave.to(this.socialLinks.current , 0.25 , {x:"-25px" , opacity:0})
            .to(this.overlay.current , 0.65 , {y:"-100%"} , "-=0.20")
            .to(this.links.current.children , 0.6, {opacity:0 , onComplete:()=>{
                done()
            }} , "-=0.45");

        }
    }

    render()
    {
        return(
            <Transition
                    in={this.props.in}
                    mountOnEnter     = {true}
                    unmountOnExit    = {true}
                    appear           = {true}
                    enter            = {true}
                    exit             = {true}
                    addEndListener = {this.onEndHandler}
                >
                <AppContext.Consumer>
                {(context)=>(
                    <div className="mobile-nav-container">
                    <div ref={this.overlay} className="overlay"></div>
                    <div className="wrapper">
                        <div className="mobile-nav">
                            <ul ref={this.links} className="mobile-nav-links">
                                <li  className="mobile-nav-link right">
                                    <NavLink className="bold uppercase" onClick={context.hideMobileNav}  to = "/" >
                                        Главная
                                    </NavLink>
                                </li>
                                <li className="mobile-nav-link left">
                                    <NavLink className="bold uppercase" onClick={context.hideMobileNav}  to = "/works">
                                        Портфолио
                                    </NavLink>
                                </li>
                                <li className="mobile-nav-link right">
                                    <NavLink className="bold uppercase" onClick={context.hideMobileNav}  to = "/single">
                                        SINGLE
                                    </NavLink>
                                </li>
                                <li className="mobile-nav-link left">
                                    <NavLink className="bold uppercase" onClick={context.hideMobileNav}  to = "/contact">
                                        Контакты
                                    </NavLink>
                                </li>
                            </ul>
                            <ul ref={this.socialLinks} className="social-nav-links no-style">
                                <li className="social-nav-link">
                                    <a href="www.facebook.com">
                                        <i className="icon icon-facebook"></i>
                                    </a>
                                </li>
                                <li className="social-nav-link">
                                    <a href="www.linkedin.com">
                                        <i className="icon icon-linkedin"></i>
                                    </a>
                                </li>
                                <li className="social-nav-link">
                                    <a href="www.behance.com">
                                        <i className="icon icon-behance"/>
                                    </a>
                                </li>
                                <li className="social-nav-link">
                                    <a href="www.instagram.com">
                                        <i className="icon icon-instagram"></i>
                                    </a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
                )}
            </AppContext.Consumer>
        </Transition>

        )
    }
}

export default MobileNav;