import React  , {Component} from "react"
import {NavLink} from "react-router-dom";
import {withContext} from "../utils/context";
import {Transition , TransitionGroup} from "react-transition-group";
import {gsap} from "gsap";
import loaderGif from "../../img/Loader.gif";
import "../../sass/components/pages/_project.scss";



class Loader extends Component
{
    render()
    {
        return (
            <div className="project-preloader-container">
                <div className="project-preloader">
                    <img src={loaderGif} alt=""/>
                </div>
            </div>
        )
    }
}
class Navbar extends Component {
    render() {
        return (      
            <nav className={`project-nav-bar-container ${this.props.className}`}>
                <div className="project-nav-bar-inner-container">
                    <div className="wrapper h-100">
                        <div className="project-nav-bar h-100">
                            <div className="container-fluid h-100">
                                <div className="row justify-content-between align-items-center h-100">
                                    <div className="col-auto">
                                        <div className="container-fluid">
                                            <div className="row align-items-center">
                                                <div className="col-auto">
                                                    <div className="logo-container">
                                                        <NavLink to="/">
                                                            <img src="../img/logo.svg" alt="tpi" className="logo"/>
                                                        </NavLink>
                                                    </div>
                                                </div>
                                                <div className="col-auto d-none d-sm-block">
                                                    <div className="project-nav-bar-social-links-container">
                                                        <ul className="project-nav-bar-social-links d-flex align-items-center no-style no-padding no-margin  ">
                                                            <li className="project-nav-bar-social-link">
                                                                <a href="www.facebook.com">
                                                                    <span className="icon icon-facebook"></span>
                                                                </a>
                                                            </li>
                                                            <li className="project-nav-bar-social-link">
                                                                <a href="www.instagram.com">
                                                                <span className="icon icon-instagram"></span>
                                                                </a>
                                                            </li>
                                                            <li className="project-nav-bar-social-link">
                                                                <a href="www.linkedin.com">
                                                                <span className="icon icon-linkedin"></span>
                                                                </a>
                                                            </li>
                                                            <li className="project-nav-bar-social-link">
                                                                <a href="www.behance.com">
                                                                    <span className="icon icon-behance"></span>
                                                                </a>
                                                            </li>
                                                        </ul>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-auto d-none d-sm-block d-md-block d-lg-block">
                                        <div className="project-nav-bar-controls-container">
                                            <ul className="project-nav-bar-controls d-flex align-items-center no-style no-padding no-margin">
                                                <li className="project-nav-bar-control-item d-flex align-items-center justify-content-center">
                                                    <NavLink to="/contact">
                                                        <button className="button white rounded">contact us now</button>
                                                    </NavLink>
                                                </li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="project-nav-bar-visibility-icon-container">
                    <span className="project-nav-bar-visibility-icon usable icon icon-up-chevron" onClick={this.props.hideProjectNavbar}/>
                </div>
            </nav>
        )
    }
}

class Project extends React.Component
{
    constructor(props)
    {
        super(props);

        this.state = 
        {
            url: this.props.url || "portfolio/yamifood/index.html",
            navbar: true,
            loaded: false,
        }

        // --- refs

        this.frame = React.createRef();

        // --- bindings 
        this.onResizeHandler = this.onResizeHandler.bind(this);
        this.hideProjectNavbar = this.hideProjectNavbar.bind(this);
        this.showProjectNavbar = this.showProjectNavbar.bind(this);
        this.toggleProjectNavBar =  this.toggleProjectNavBar.bind(this);
        this.onFrameLoadHandler = this.onFrameLoadHandler.bind(this);

    }

    onSourceUpdate()
    {

    }

    componentDidMount()
    {
        this.onResizeHandler();

        if(this.props.app)
        {
            this.props.app.hideNavigation();
        }

        window.addEventListener("resize" , this.onResizeHandler);
        document.body.style.overflow="hidden";
    }

    componentWillUnmount()
    {
        if(this.props.app)
        {
            this.props.app.showNavigation();
        }

        document.body.style.overflow="";
        window.removeEventListener("resize" , this.onResizeHandler);
    }

    onResizeHandler()
    {
        if(!this.frame.current)    
        {
            throw new Error("cannot find ref to frame component");
        }

        let navbar = document.querySelector("nav.project-nav-bar-container");

        if(navbar && this.state.navbar)
        {
            this.frame.current.height = window.innerHeight - navbar.offsetHeight;
        }
        else 
        {
            this.frame.current.height = window.innerHeight;
        }


    }

    onFrameLoadHandler()
    {
        this.setState({
            loaded:true,
        })
    }

    componentDidUpdate()
    {
        this.onResizeHandler();
    }

    showProjectNavbar()
    {
        if(!this.state.navbar)
        {
            this.setState({
                navbar:true
            });
        }
    }


    hideProjectNavbar()
    {
        if(this.state.navbar)
        {
            this.setState({
                navbar: false
            })
        }
    }


    toggleProjectNavBar()
    {
        this.setState({
            navbar: !this.state.navbar
        })
    }
    


    render(props)
    {
        return(
            <div className="page project-page">
                <Navbar className={this.state.navbar ? 'active' : 'hidden'} hideProjectNavbar={this.toggleProjectNavBar}/> 
                    <Loader/>
                <div className={`frame-container ${this.state.navbar ? "active-nav-bar" : "hidden-nav-bar"}`}>
                    <iframe onLoad={this.onFrameLoadHandler} title="project-preview" ref={this.frame}  src={this.state.url} frameBorder="0"></iframe>
                </div>
            </div>
        )
    }
}

export default withContext(Project);
