/**
 * 
 * @param {HTMLElement} target HTMLElement
 * @param {boolean} header calculate height with header height 
 * @throws Error: target element is not instance of HTMLElement
 * @description Устанавливает высоту заданного элемента
 * 
 */

export function setFullHeight(target , header , min=false)
{
    if(!target)
    {
        throw new Error("target is null or undefined");
    }

    if(!(target instanceof HTMLElement))
    {
        throw new Error("target element is not instance of HTMLElement");
    }

    let height = 
    {
        window : window.innerHeight,
        header : header ? ( document.querySelector("header") ? document.querySelector("header").offsetHeight : 0) : 0
    }

    if(min)
    {
        target.style.minHeight = height.window - height.header + 'px';
    }

    else
    {
        target.style.height = height.window - height.header + 'px';
    }

}
