import React, {Component} from 'react';
import TransitionGroup from 'react-transition-group/TransitionGroup';
import { Switch, BrowserRouter , Route , } from 'react-router-dom';
import Navigation from '../js/components/general/Navigation';
import MobileNav  from '../js/components/general/MobileNav';
import Works from '../js/pages/Works'
import Home from '../js/pages/Home'
import Project from '../js/pages/Project'
import Contact from '../js/pages/Contact'
import AppContext from "../js/utils/context";

// -- styles -- //
import 'bootstrap/scss/bootstrap-reboot.scss';
import 'bootstrap/scss/bootstrap-grid.scss';





class App extends Component {

    constructor(props)
    {
        super(props);
        this.state={
            mobile_navigation: false,
            navigation: true,
        }
    
        // ---- bindings ---- //

        this.toggleNavigation = this.toggleNavigation.bind(this);
        this.showNavigation = this.showNavigation.bind(this);
        this.hideNavigation = this.hideNavigation.bind(this);

        this.toggleMobileNav = this.toggleMobileNav.bind(this);
        this.showMobileNav = this.showMobileNav.bind(this);
        this.hideMobileNav = this.hideMobileNav.bind(this);

    }


    // -- Context methods  -- //

    toggleNavigation()
    {
        this.setState({
            navigation: !this.state.navigation
        })
    }
    showNavigation()
    {
        if(!this.state.navigation)
        {
            this.setState({
                navigation:true,
            });
        }
    }

    hideNavigation()
    {
        if(this.state.navigation)
        {
            this.setState({
                navigation: false
            })
        }
    }

    
    toggleMobileNav()
    {
        this.setState({
            mobile_navigation: !this.state.mobile_navigation
        });
    }


    showMobileNav()
    {
        if(!this.state.mobile_navigation)
        {
            this.setState({
                mobile_navigation:true,
            });
        }
    }

    hideMobileNav()
    {
        if(this.state.mobile_navigation )
        {
            this.setState({
                mobile_navigation:false,
            })
        }
    }

    
    render() {
        return (
            <AppContext.Provider value={{
                navigation          : this.state.navigation,
                mobile_navigation   : this.state.mobile_navigation,
                toggleMobileNav     : this.toggleMobileNav,
                showMobileNav       : this.showMobileNav,
                hideMobileNav       : this.hideMobileNav,
                toggleNavigation    : this.toggleNavigation,
                showNavigation      : this.showNavigation,
                hideNavigation      : this.hideNavigation,

            }}>
                <BrowserRouter>
                    <div>
                        <TransitionGroup>
                            {this.state.navigation ? <Navigation /> : '' }
                            {this.state.mobile_navigation ? <MobileNav onMenuClick={this.onMenuClickHandler} /> : null}
                        </TransitionGroup>
                        <Switch>
                            <Route exact path="/" component={Home} />
                            <Route exact path="/works" component={Works} />
                            <Route exact path="/single" component={Project} />
                            <Route exact path="/contact" render={(props)=> <Contact {...props} current={0}/>}/>
                            <Route exact path="/contact/email" render={(props)=> <Contact {...props} current={1}/>}/>
                            <Route exact path="/contact/phone" render={(props)=> <Contact {...props} current={2}/>}/>
                            <Route exact path="/contact/name" render={(props)=> <Contact {...props} current={3}/>}/>
                            <Route exact path="/contact/message" render={(props)=> <Contact {...props} current={4}/>}/>
                            <Route exact path="/contact/submit" render={(props)=> <Contact {...props} current={5}/>}/>
                        </Switch>
                    </div>
                </BrowserRouter>
            </AppContext.Provider>
        )
    }
}

export default App;