

import React, { Component } from 'react'


export class UnderlineInput extends Component
{
    constructor(props)
    {
        super(props)

        this.state=
        {
            selected: false,
            valid: true,
            allowEmpty: this.props.allowEmpty
        }


        // ---- refs ---- //

        this.input = React.createRef();

        // ---- bindings ---- //

        this.onBlurHandler =  this.onBlurHandler.bind(this);
        this.onFocusHandler = this.onFocusHandler.bind(this);
        this.onKeyDownHandler = this.onKeyDownHandler.bind(this);
        this.validate = this.validate.bind(this);
        this.onChangeHandler = this.onChangeHandler.bind(this);
    }


    onChangeHandler(e)
    {
        const value = this.input.current.value;
        if(this.props.onInput)
        {
            this.props.onInput(value);
        }
        if(this.validate(value))
        {
            if(!this.state.valid)
            {
                this.setState({valid: true});
            }

            if(this.props.onValid)
            {
                this.props.onValid(value);
            }
            
        }

        if(!this.validate(value))
        {
            if(this.state.valid)
            {
                this.setState({valid: false});
            }

            if(this.props.onInvalid)
            {
                this.props.onInvalid(value);
            }
        }
    }

    validate(value)
    {

        if(value === "")
        {
            if(this.state.allowEmpty)
            {
                return true;
            }
            
            return false;
        }

        return true;
    }

    onKeyDownHandler(e)
    {
        const value = this.input.current.value;

        if(e.which === 13 || e.keyCode === 13)
        {
            if(!this.props.onSubmit)
            {
                return;
            }

            if(this.validate(value))
            {
                this.props.onSubmit(value);
            }

        }
    }

    onBlurHandler(e)
    {

        const value = this.input.current.value;

        let state = {
            selected: false,
        };

        if(this.validate(value))
        {
            if(!this.state.valid)   
            {
                state.valid = true;
            }

            if(this.props.onValid)
            {
                this.props.onValid(value);
            }
        } 
        else
        {
            if(this.state.valid)   
            {
                state.valid = false;
            }

            if(this.props.onInvalid)
            {
                this.props.onInvalid(value);
            }
        }

        this.setState(state);
    }

    onFocusHandler(e)
    {
        this.setState({
            selected:true,
        });
    }


    render()
    {
        return (
            <div className={`underline-input-group ${this.state.selected ? 'active' : ''} ${!this.state.valid ? "validation-error" : "validated"}`}>
                <input ref={this.input} type="text" defaultValue={this.props.defaultValue} placeholder={this.props.placeholder}
                onKeyDown={this.onKeyDownHandler}
                onFocus={this.onFocusHandler}
                onBlur={this.onBlurHandler}
                onChange={this.onChangeHandler}                
                />
            </div>
        )
    }
}


export class EmailUnderlineInput extends UnderlineInput
{
    validate(value)
    {
        super.validate(value);

        let expression = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/;
        return expression.test(value);
    }
}

export class PhoneUnderlineInput extends UnderlineInput
{
    validate(value)
    {
        super.validate(value);

        if(value.match(/\d/g) === null)
        {
            return false;
        }

        return value.match(/\d/g).length > 7;
    }
}