import React from 'react'
import PropTypes from 'prop-types';
import {setFullHeight} from "../utils/functions";
import { TimelineLite } from "gsap";
import {Transition , SwitchTransition} from "react-transition-group";



// ---- styles ---- //

import '../../sass/components/pages/_works.scss';


// ---- pictures ---- //

import  btcu    from   '../../img/works/btcu.svg';
import  jambo   from '../../img/works/jambo.svg';
import  lacto   from '../../img/works/lacto.png';
import  pigeon  from '../../img/works/pigeon.svg';
import  sbf     from '../../img/works/sbf.png';


class ProgressBar extends React.Component
{
    constructor(props)
    {
        super(props);

        this.state = {
            length: this.props.length || 0,
            current : this.props.current || 0,
            delay : this.props.delay || 8,
        }

        // bindings 

        this.barClickHandler = this.barClickHandler.bind(this);
        this.onComplete = this.onComplete.bind(this);

        
        // vars 
        this._progress = null;

        // refs
        this.currentBar = React.createRef();
    }


    // ---- lyfe-cycle events ---- //

    componentDidMount()
    {
        this._progress = new TimelineLite({onComplete: this.onComplete});
        this._progress.fromTo(this.currentBar.current , this.state.delay , {width: "0%"} , {width: "100%"});
    }

    componentWillUnmount()
    {
        this._progress.kill();
    }


    shouldComponentUpdate()
    {
        return false;
    }

    onComplete()
    {
        if(!this.props.onComplete)
        {
            return;
        }

        this.props.onComplete();
    }

    barClickHandler(e)
    {
        if(!this.props.barClick)
        {
            return;
        }

        this.props.barClick(e);
    }


    render()
    {

        let bars = new Array(this.props.length).fill().map( (bar , index) => {

            if(index === this.props.current)
            {
                return(
                    <div key={index} data-index={index} className="our-works-progress-bar-container align-self-center usable" onClick={this.barClickHandler}> 
                        <div className="our-works-progress-bar-track">
                            <div ref={this.currentBar} className="our-works-progress-bar" style={{width: '0%'}}/>
                        </div>
                    </div>
                )
            }

            if(index > this.props.current)
            {
                return(
                    <div key={index} data-index={index} className="our-works-progress-bar-container align-self-center usable" onClick={this.barClickHandler}> 
                        <div className="our-works-progress-bar-track">
                            <div className="our-works-progress-bar" style={{width: 0 + '%'}}/>
                        </div>
                    </div>
                )
            }

            return (
                    <div key={index} data-index={index} className="our-works-progress-bar-container align-self-center usable" onClick={this.barClickHandler}> 
                        <div className="our-works-progress-bar-track">
                            <div className="our-works-progress-bar" style={{width: 100 + '%'}}/>
                        </div>
                    </div>
                );
        });

        return(
            <div className="our-works-progress-bars d-flex">
                <span className="bold uppercase">
                    {(this.state.current < 10 ? "0" : "" ) + (this.state.current + 1)}
                </span>
                {bars}
                <span className="bold uppercase">
                {(this.state.length < 10 ? "0" : "" ) + this.state.length}
                </span>
            </div>
        )
    }
}

ProgressBar.propTypes = {
    length: PropTypes.number.isRequired,
    current: PropTypes.number.isRequired,
    barClickHandler : PropTypes.func.isRequired,
}


class Work extends React.Component
{

    constructor(props)
    {
        super(props);

        this.picture = React.createRef();
        this.pre_title = React.createRef();
        this.name = React.createRef();
        this.surname = React.createRef();
        this.description = React.createRef();
        this.nextButton = React.createRef();

        // ---- vars ---- //

        this._slideOut = null;
        this._slideIn = null;
        this._intro   = null;


        // ---- bindings  ---- //

        this.onTransitionEnd = this.onTransitionEnd.bind(this);

    }

    // ---- methods ---- //



    // ---- event listeners ---- //

    onTransitionEnd(n , done)
    {
        if(!this.props.in)
        {
            this._slideOut = new TimelineLite();
            this._slideOut.eventCallback("onComplete" , ()=>{
                this._slideOut.kill();
                done();
            })

            this._slideOut.fromTo(this.picture.current , {x:"0%" , y:"0%" , opacity:1} , {x:"-25%", y:"-25%", scale:0.5, opacity: 0.5})
            .to(this.picture.current , 0.25, {x:"-50%" , y:"-50%" , opacity:0})
            .to([this.pre_title.current , this.description.current], 0.25 , {opacity:0})
            .to(this.surname.current , 0.25, {opacity:0})
            .to(this.name.current , 0.25, {opacity:0})
            this._slideOut.play(0);
        }
        else
        {
            this._slideIn = new TimelineLite()
            this._slideIn.eventCallback("onComplete" , ()=>{
                this._slideIn.kill();
                done();
            }) 
            this._slideIn.fromTo(this.picture.current , 0.45 , {x:'100%' , y:'100%' , opacity: 0 , scale:1} , {x:'0%' , y:'0%' , opacity:1 ,scale:1});
            this._slideIn.fromTo(this.name.current , 0.25 , {opacity:0} , {opacity:1});
            this._slideIn.fromTo(this.surname.current , 0.25 , {opacity:0} , {opacity:1});
            this._slideIn.fromTo(this.pre_title.current , 0.25 , {opacity:0} , {opacity:1});
            this._slideIn.fromTo(this.description.current , 0.25 , {opacity:0} , {opacity:1} , "-=0.25");   
            this._slideIn.play(0);
        }
    }

    render()
    {
        let current = this.props.project || {};

        return(
            <Transition 
            in={this.props.in}
            mountOnEnter={true}
            unmountOnExit={true}
            appear={true}
            enter={true}
            exit={true}
            addEndListener={this.onTransitionEnd}
            onExited={this.props.onExited}
            onEntered={this.props.onEntered}
            timeout={2000}
            >
            <div className="our-works-inner d-flex flex-column justify-content-center h-100">
                <div className="our-works-content d-flex align-items-center h-100">
                    <div className="our-work">
                        <div ref={this.picture} className="our-work-picture" style={{backgroundImage:`url(${current.img})`}}/>
                        <div className="our-work-meta d-flex flex-column justify-content-center">
                            <div ref={this.pre_title} className="our-work-pre-title">portfolio</div>
                            <h2 ref={this.name} className="our-work-name bold uppercase">{current.name}</h2>
                            <h2 ref={this.surname} className="our-work-surname bold uppercase">{current.surname}</h2>      
                        </div>
                        <button ref={this.nextButton} onClick={this.props.next} className="our-work-next-button bold bold uppercase usable">
                            Дальше
                        </button>   
                    </div>
                </div>
                <div className="our-works-footer d-flex align-items-center justy-content-center">
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-3 col-md-4 col-lg-4">
                                <div ref={this.description} className="our-works-work-description">{current.description}</div>
                            </div>
                            <div className="col-9 col-md-4 col-lg-4 d-flex align-items-center justify-content-center">
                                <ProgressBar barClick={this.props.barClickHandler} length={this.props.length} current={this.props.current} onComplete={this.props.onComplete}/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Transition>
        )
    }
}


Work.propTypes = {
    project         :   PropTypes.object.isRequired,
    current         :   PropTypes.number.isRequired,
    length          :   PropTypes.number.isRequired,
    barClickHandler :   PropTypes.func.isRequired,
    next            :   PropTypes.func.isRequired,
}


export default class Works extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            show:this.props.show || false,
            current: 0,
            progress: 0,
            delay:  this.props.delay || 8000,
            projects: 
            [
                {
                    surname: 'Lactomarin',
                    description: 'promo landing',
                    img: lacto,
                    link: 'works/lacto',
                }, 
                {
                    name: 'SBF',
                    surname: 'SERVICE',
                    description: 'promo landing',
                    img: sbf,
                    link: 'works/sbf',
                },
                {
                    name: 'FAST',
                    surname: 'PIGEON',
                    description: 'delivery service',
                    img: pigeon,
                    link: 'works/fastpigeon',
                },
                {
                    name: 'BTCU',
                    description: 'crypto exchange',
                    img: btcu,
                    link: 'works/btcu',
                },
                {
                    name: 'Jambo',
                    description: 'kids shop',
                    img: jambo,
                    link: 'works/jambo',
                }
            ],
        };


        
        // ---- REF'S ---- //

        this.container = React.createRef();

        // ---- vars ---- //

        this._isAnimating = false;
        this._entered = false;
        this._intro     = new TimelineLite({paused:true});



        // ---- bindings ---- //

        this.init = this.init.bind(this);
        this.updateHeight = this.updateHeight.bind(this);
        this.barClickHandler = this.barClickHandler.bind(this);
        this.onEndHandler = this.onEndHandler.bind(this);
        this.onComplete = this.onComplete.bind(this);

        // carousel methods

        this.stop = this.stop.bind(this);
        this.play = this.play.bind(this);
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);
        this.keyDownEventHandler = this.keyDownEventHandler.bind(this);

    }

    onComplete(callback)
    {

        this._intro.kill();
        this._slideIn.kill();
        this._slideOut.kill();
        this._exit.kill();
        this._entered = !this._entered;
        if(callback) callback();
    }

    updateHeight()
    {
        try {
            setFullHeight(this.container.current , false);            
        } catch (error) {
            console.error(error)
        }

    }

    keyDownEventHandler(e)
    {
        switch(e.key)
        {
            case "ArrowRight": this.next(); break;
            case "ArrowLeft" : this.previous(); break;
            default: break;
        }
    }

    // ---- slider methods ---- //

    stop()
    {
       
    }

    play()
    {
      
        
    }

    next()
    {
        this.setState({
            progress: 0,
            current: (this.state.current + 1) >= this.state.projects.length ? 0 : this.state.current+1
        });

    }

    previous()
    {

        this.setState({
            progress: 0,
            current: (this.state.current - 1) < 0 ? this.state.projects.length-1 : this.state.current-1
        });
    }


    // ---- event handlers ---- //

    barClickHandler(e)
    {
        
        let index = Number.parseInt(e.currentTarget.dataset.index);
        if(this.state.current !== index)
        {
            this.setState({current : index});
        }
    }

    // ---- lyfe-cycle events ---- //

    componentDidMount()
    {
        this.updateHeight();


        window.addEventListener("resize" , this.updateHeight);
        window.addEventListener("keydown" , this.keyDownEventHandler);
        //this.init();
    }

    componentWillUnmount()
    {
        window.removeEventListener("resize" , this.updateHeight);
        window.addEventListener("keydown" , this.keyDownEventHandler);
    }


    init()
    {
        this._intro.fromTo(this.picture.current , 0.45 , {x:'100%' , y:'100%' , opacity: 0} , {x:'0%' , y:'0%' , opacity:1});
        this._intro.fromTo(this.name.current , 0.25 , {opacity:0} , {opacity:1});
        this._intro.fromTo(this.surname.current , 0.25 , {opacity:0} , {opacity:1});
        this._intro.fromTo(this.pre_title.current , 0.25 , {opacity:0} , {opacity:1});
        this._intro.fromTo(this.description.current , 0.25 , {opacity:0} , {opacity:1} , "-=0.25");
        this._intro.fromTo(this.nextButton.current , 0.25 , {y:'100%' , opacity:0} , {y:0 , opacity:1} , "-=0.10");

        this._intro.play(0);
        this._entered = true;

    }

    onEndHandler(n , done)
    {

    }


    render() {

        let current = this.state.projects[this.state.current];
        return (
            <div ref={this.container} className="our-works-container">
                <div className="wrapper h-100">
                    <div className="our-works justify-content-center h-100">
                        <SwitchTransition>
                            <Work key={this.state.current}  project={current} length={this.state.projects.length} current={this.state.current} onComplete={this.next}  
                                barClickHandler={this.barClickHandler}
                                next={this.next}

                            />
                        </SwitchTransition>
                    </div>
                </div>
            </div>
        );
    }
}
