class WatchOurProjects extends React.Component
{
    constructor(props)
    {
        super(props);
        this.container = React.createRef();
        this.updateHeight = throttle(this.updateHeight.bind(this), 300);
    }

    updateHeight()
    {
        setFullHeight(this.container.current , true);
    }


    componentDidMount()
    {
        this.updateHeight();
        window.addEventListener("resize" , this.updateHeight);
    }

    componentWillUnmount()
    {
        window.removeEventListener("resize" , this.updateHeight);
    }




    render()
    {
        return(
        <div className="watch-our-projects-container">
            <div className="wrapper">
                <div ref={this.container} className="watch-our-projects d-flex aling-items-center justify-content-center">
                    <div className="button-container d-flex align-items-center justify-content-center">
                        <button className="button white">
                            <NavLink to="/works" className={"black"}>
                                Watch our projects
                            </NavLink>
                        </button>
                    </div>
                </div>
            </div>
            <div className="background">
                <div className="digit">03</div>
            </div>
        </div>
        );
    }
}

